body{
  font-size: $size-m;
  font-family: $main-font, sans-serif;
  background-color: map-get($colors, body-color);
  color: map-get($colors, dark-color);
  // padding-bottom: 30rem;
  @include scrollMinimal();
}

img{
  max-width: 100%;
  height: auto;
  display: block;
}

a{
  text-decoration:none;
}

ul{
	list-style: none;
}

h1, h2, h3, h4, h5, section{
  position: relative;
}

p{
  font-size: $size-sm;
}