.about-section{
	.header-section{
		@include from-l(){
			padding-left: 10%;
			padding-right: 1rem;
			margin-bottom: 0;
		}
	}
	
	.img-section{
		@include to-m(){
			max-height: 25rem;
		}

		@include from-m(){
			position: absolute;
			top: 0;
			right: 0;
			width: 50%;
			height: 100%;			
		}

		&.img-left{
			@include from-m(){
				right: unset;
				left: 0;
			}			
		}
	}

}