.toggle-menu{
	position: absolute;
  top: -1px;
  right: -1px;
  height: calc(100% + 2px);
	// right: 1rem;
	font-size: 22px;
	padding: 20px;
	background-color: rgba(#000, .9);
	color: #fff;
	cursor: pointer;
	// @extend .bg-diagonal;

  .hamburger {
    position: relative;
    width: 30px;
    height: 3px;
    background: #fff;
    display: block;
    transform-origin: center;
    transition: .5s ease-in-out;

    &:after,
    &:before {
      transition: .5s ease-in-out;
      content: "";
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      background: #fff;
    }
    &:before {
      top: -10px;
    } 
    &:after {
      bottom: -10px;
    }
  }	

  &.menu-close{
    border-radius: 0;
    .hamburger {
      transform: rotate(45deg);
      &:after {
        transform: rotate(90deg);
        bottom: 0;
      }
      &:before {
        transform: rotate(90deg);
        top: 0;
      }
    }  	
  }

}

.main-logo{
	position: relative;

	@include from-l(){
		display: block;
	}

	.toggle-menu{
		@include from-m(){
			display: none;
		}
	}
}

.off-canvas-layer{
	position: fixed;
  top: calc(4rem - 1px);
  left: 0;
  bottom: 0;
  right: 0;
  z-index: $z-fixed - 1;
  cursor: pointer;
  display: none;
  background-color: rgba(#000, 0.5);

  &.show{
  	display: block;

  	@include from-m(){
			display: none;
		}
  }
}

.main-menu{
	padding-right: 0;
	width: 100%;

	@include to-m(){
		position: fixed;
		top: calc(4rem - 1px);
		left: -100%;
		width: 75% !important;
		height: calc(100vh - 4rem + 1px);
		background-color: #fff;
		z-index: $z-fixed;
		overflow-y: auto;
		transition: left .3s;

		&.show{
			left: 0;
		}
	}

	li{
		position: relative;	
	}

	&__item{
		@include from-m(){
			&::before{
				content: '';
				position: absolute;
				top: 3rem;
				width: 0;
				max-width: 60px;
				height: 2px;
				left: 50%;
				transform: translateX(-50%);
				background-color: map-get($colors, color1);
				z-index: $z-normal;
				transition: all .2s;
			}		
		}

		&:hover, &:active, &:focus{
			> a{
				color: darken(map-get($colors, color1), 10%);
			}

			// @include from-m(){
			// 	&::before{
			// 		width: 50%;
			// 	}
			// }
		}			
	}

	@include from-m(){
		& > &__item{
			&:hover, &:active, &:focus{
				&::before{
					width: 50%;
				}
			}
		}
	}

	&__link{
    position: relative;
		font-size: $size-s;
		font-weight: $weight-m;
	}

	a{
		background-color: #fff;	
		color: map-get($colors, text-color);
		font-size: $size-s;
		transition: color .2s;

		&:hover, &.active{
			color: darken(map-get($colors, color1), 10%);
		}

		.i-link{
			margin-left: 4px;
			vertical-align: middle;
		}
	}

	li{
		li{
			max-height: 0;
			overflow: hidden;
			transition: all .5s;
		}

		@include to-m(){
			&.open > a{
				color: darken(map-get($colors, color1), 10%);
			}

			&.open > ul > li{
				max-height: 1000px;
				overflow: visible;			
			}
		}

		@include from-m(){
			&:hover > ul > li{
				max-height: 1000px;
				overflow: visible;
			}
		}

		.sub-menu{
			margin-left: .25rem;

			li:hover, li:active, li:focus{
				> a{
					color: darken(map-get($colors, color1), 10%);
				}
			}	

			@include from-m(){
				position: absolute;
				top: 100%;
				left: 0;
				width: 110%;
				box-shadow: 0 6px 12px rgba(0,0,0,.175);

				a{
					min-width: 100%;
					margin: 0;
					padding: 0 1rem;
					text-align: left;
					border-bottom: 1px solid rgba(#000, .3);
				}

			 .sub-menu{
			 		top: 0;
					left: 100%;

					&.from-right{
						margin-left: 0;
						margin-right: .25rem;
						left: -100%;
					}
				}
			}
		}
	}

	@include to-m(){
		& > li > a{
			border-bottom: 1px solid rgba(#000, .3);
		}

		& > li > .sub-menu{
			margin-left: 2rem;

			li .sub-menu{
				margin-left: 1rem;
			}
		}
	}
}