
.carousel-item{	
	&__content{
		color: #fff;
	}

	&__link{
    position: relative;
		display: block;
	}

	&__data, &__tags{
		position: absolute;
		// width: 100%;
		padding: 1rem;
		transition: all .3s;

		&:hover{
			color: darken(map-get($colors, color1), 10%);
		}
	}

	&__tags{
		right: 0;
		top: 0;
	}	

	&__data{
		left: 0;
		bottom: 0;
	}			
}