.main-banner{
	position: relative;
	background-color: #000;
	margin-top: 4rem;

	@include from-m-To-l{
		margin-top: 7.25rem;
	}

	&__img{
		position: absolute;
		top: 0;
		@extend .img-cover;
		opacity: 0.7;
	}

	&__data{
		position: relative;
		color: #fff;
		padding: 6rem 0;
		transform: translateY(-2.5%);

		@include from-m(){
			width: 80%;
		}

		@include from-l(){
			padding: 10rem 0;
			width: 60%;
		}
	}

	&__title{
		font-weight: $weight-b;
		font-size: $size-xxl;
		margin-bottom: .75rem;

		@include to-m(){
			font-size: $size-xl;
		}
	}

	&__subtitle{
		font-weight: $weight-m;
		font-size: $size-l;
		color: map-get($colors, color1);
	}

	&__description{
		margin: .5rem 0 1.5rem;
	}

	&__subtitle,
	&__description{
		@include to-m(){
			display: none;
		}
	}
}