.gallery-section{
	// background-color: #000;
	color: #fff;
}

.gallery-ml{
	display: none;
}

.gallery-item{
	// &__content{
	// 	color: #fff;
	// }

	&:hover {
		.gallery-item__data, .gallery-item__tags{
			opacity: 1;
		}
	}

	&__data, &__tags{
		position: absolute;
		left: 0;
		padding: 1rem;
		transition: all .3s;
		opacity: 0;

		&:hover{
			color: darken(map-get($colors, color1), 10%);
		}
	}

	&__tags{
		top: 0;
	}	

	&__data{
		bottom: 0;
	}		
}