.carousel-module {
	position: relative;
}

.carousel-container {
	position: relative; 
	overflow: hidden;
	padding-bottom: 20rem;

	@include from-l(){
		padding-bottom: 27.5rem;
	}
}

.arrow-left, .arrow-right {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
	border: none;
	padding: 1rem;
	background-color: #000;
	color: #fff;
	cursor: pointer;
	z-index: $z-normal;
	font-size: $size-l;
	opacity: .7;
	transition: all .3s;
	@extend .cross-center;

	&:hover{
		background-color: map-get($colors, color1);
		color: #000;
		opacity: 1;
	}
}

.arrow-left{
	left: 0;
}

.arrow-right {
	right: 0;
}

.carousel-item {
	@extend .absolute-container;
	cursor: pointer;
	transition: transform .3s;

	$max-images: 100;
  @for $i from 1 through $max-images {
    &:nth-of-type(#{$i}) {
			transform: translate3d(calc(100% * #{$i} - 200%), 0, 0%);
		}
  }  
}

.carousel-type-items{
	.carousel-item{
	  @include from-m(){
	  	width: 50%;
	  }

	  @include from-l(){
	  	width: 33.333333333333%;
	  }

	  @include from-xl(){
	  	width: 25%;
	  }    		
	}
}