// PALETA DE COLORES
	$colors: (
	 	color1: #FFDA44,
		color2: #0094D9,
		color3: #BD5C30,
		color4: #24272D,
		default: #bdc3c7,
		primary: #3498db,
		success: #2ecc71,
		info:		 #1abc9c,
		warning: #f1c40f,
		danger:  #e74c3c,
		text-color: #53575a,
		dark-color: #282d31,
		body-color: #F7F7F7,
		border-color: #efefef
	);
		// body: #fcfcfc,

	// IMPRIMIR COLORES COMO CLASES
	@each $color, $value in $colors{
		.#{$color}{
			color: $value;
		}
		.bg-#{$color}{
			background-color: $value;
		}
	}

	.black{ color: #000; }
	.bg-black{ background-color: #000; }

	.white{ color: #fff; }
	.bg-white{ background-color: #fff; }	

