.lightbox-overlay {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.85);
  z-index: 1000;
}

.lightbox-close,
.lightbox-arrow-left,
.lightbox-arrow-right {
  position: absolute;
  border: none;
  padding: 1rem;
  background-color: transparent;
  color: #fff;
  cursor: pointer;
  z-index: 1;
  font-size: 24px;
  transition: all .3s;
}

.lightbox-close:hover,
.lightbox-arrow-left:hover,
.lightbox-arrow-right:hover {
  background-color: #000;
  color: #fff;
}

.lightbox-close {
  top: 1rem;
  right: 1rem;
  border-radius: 50%;
}

.lightbox-arrow-left,
.lightbox-arrow-right {
  top: 50%;
  transform: translateY(-50%);
  font-size: 28px;
  padding: 2rem 1rem;
}

@media screen and (min-width: 1024px) {
  .lightbox-arrow-left,
  .lightbox-arrow-right {
    padding: 3rem 1.5rem;
  }
}

.lightbox-arrow-left {
  left: 1rem;
}

.lightbox-arrow-right {
  right: 1rem;
}

.lightbox-item__img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: auto;
  transform: translate(-50%, -50%);
  max-width: 90%;
  height: auto;
  max-height: calc(100% - 8rem);
}

@media screen and (min-width: 1024px) {
  .lightbox-item__img {
    height: 100%;
  }
}

.lightbox-item__data {
  color: #fff;
  position: absolute;
  left: 50%;
  bottom: 1.75rem;
  transform: translateX(-50%);
}

.logo {
  color: #282d31;
}

.logo__img {
  display: inline-block;
  width: 50px;
  padding: 10px 0;
}

.logo__title {
  display: inline-block;
  margin-left: 5px;
}

.main-header {
  background-color: #fff;
}

.main-nav {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: #fff;
  border: 1px solid #efefef;
}

.toggle-menu {
  position: absolute;
  top: -1px;
  right: -1px;
  height: calc(100% + 2px);
  font-size: 22px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.9);
  color: #fff;
  cursor: pointer;
}

.toggle-menu .hamburger {
  position: relative;
  width: 30px;
  height: 3px;
  background: #fff;
  display: block;
  transform-origin: center;
  transition: .5s ease-in-out;
}

.toggle-menu .hamburger:after, .toggle-menu .hamburger:before {
  transition: .5s ease-in-out;
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: #fff;
}

.toggle-menu .hamburger:before {
  top: -10px;
}

.toggle-menu .hamburger:after {
  bottom: -10px;
}

.toggle-menu.menu-close {
  border-radius: 0;
}

.toggle-menu.menu-close .hamburger {
  transform: rotate(45deg);
}

.toggle-menu.menu-close .hamburger:after {
  transform: rotate(90deg);
  bottom: 0;
}

.toggle-menu.menu-close .hamburger:before {
  transform: rotate(90deg);
  top: 0;
}

.main-logo {
