// BACKGROUND
	// FONDO DARK DIAGONAL
		.bg-diagonal{
			&::before{
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: linear-gradient(to right bottom, transparent 50%, #000 50%);
				opacity: .4;
			}
		}

		.bg-diagonal2{
			&::after{
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: linear-gradient(to right bottom, transparent 50%, #000 50%);
				opacity: .4;
			}
		}

	// HOVER DARK
		.hover-dark{
			position: absolute;
			top: 0;
			width: 100%;
			height: 100%;

			&--h-auto{
				height: auto;
			}

			&::before{
				content: '';
				position: absolute;
		    top: 0;
		    right: 0;
		    bottom: 0;
		    left: 0;
		    background-image: linear-gradient(180deg,rgba(#000,.4) 0,transparent 40%,transparent 60%,rgba(#000,.3));
		    pointer-events: none;
				opacity: 0;
				transition: opacity .2s;
			}

			&:hover{
				&::before{
					opacity: 1;
				}
			}
		}

// IMAGE
	// IMAGEN COVER
		.img-cover{
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

// OVERFLOW
	.not-scroll-v{
		overflow-y: hidden; 
	}

// LAYOUT
	// EMBED CONTAINER
		.embed-container {
		  position: relative; 
		  margin: 0 auto;
		  padding-bottom: 56.25%;  /* 16:9 */

			iframe {
		    position: absolute; 
		    top:0; 
		    left: 0; 
		    width: 100%; 
		    height: 100%;
			}
		}

		.absolute-container{
			position: absolute;
			top: 0;
			width: 100%;
			height: 100%;
		}

// PADDINGS
	// PADDING VERTICAL
		.padding-container{
			padding-top: 2.5rem;
			padding-bottom: 2rem;

			@include from-l(){
				padding-top: 4rem;
				padding-bottom: 4rem;				
			}
		}

// SECTION
	// HEADER SECTION
		.header-section{

			margin-bottom: 1.5rem;

			&__title{
				margin-bottom: .75rem;

				@include to-m(){
					font-size: $size-xl;
				}
			}

			&__subtitle{
				font-weight: $weight-m;
				font-size: $size-l;
			}

			&__description{
				margin: .5rem 0 1.5rem;
			}
		}

	// HEADER BG IMAGE
		.bg-img-section{
			position: absolute;
			top: 0;
			@extend .img-cover;
			opacity: 0.4;
		}


// CUT PAGE OR CONTENT
	.cut-polygon{
		clip-path: polygon(0 0, 100% 0, 100% 100%, 0 94%);
	}

// TEXT
	.sub-line{
		--gap: 1.75rem;
		margin-bottom: var(--gap);

		&::before{
			content: '';
			position: absolute;
			bottom: calc( -1 * (var(--gap) / 2.25 ));
			left: 0;
			width: 37.5px;
			height: 2px;
			border-radius: 2px;
			background-color: map-get($colors, color1);
		}

		&.line-center{
			&::before{
				left: 50%;
				transform: translateX(-50%);
			}
		}
	}