.button{
	font-size: $size-s;
	position: relative;
	transition: background-color .2s;
	
	@include from-m(){
		font-size: $size-m;
	}

	&--b-radius{
		border-radius: 3px;
	}

	&--g-diagonal{
		&::before{
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: linear-gradient(to right bottom, transparent 50%, #000 50%);
			opacity: .4;
		}
	}

	&--size-l{
		@include from-l(){
			padding: 1rem 1.5rem;
		}
	}

	&--normal{
		background-color: map-get($colors, color1);
		color: map-get($colors, dark);

		&:hover{
			background-color: darken(map-get($colors, color1), 10%);
		}
	}

	&--transparent{
		background-color: transparent;
		border: 1px solid #fff;
		color: #fff;
    padding: .55em 1.2em;

		@include from-l(){
			padding: .925rem 1.5rem;
		}
		
		&:hover{
			background-color: #fff;
			color: #000;
		}
	}

	&--dark{
		background-color: map-get($colors, dark-color);
		color: #fff;

		&:hover{
			background-color: #fff;
			color: map-get($colors, dark-color);
		}
	}

	&--light{
		background-color: #fff;
		color: map-get($colors, dark-color);

		&:hover{
			background-color: map-get($colors, dark-color);
			color: #fff;
		}
	}	
}