.lightbox-overlay{
	position: fixed;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(#000, .85);
	z-index: $z-modal;
}

.lightbox-container{
	// position: absolute;
	// top: 0;
 //  left: 50%;
 //  width: 60%;
 //  height: 100%;
 //  transform: translateX(-50%);
}

.lightbox-close,
.lightbox-arrow-left, 
.lightbox-arrow-right {
  position: absolute;
	border: none;
	padding: 1rem;
	background-color: transparent;
	color: #fff;
	cursor: pointer;
	z-index: $z-normal;
	font-size: $size-xl;
	// opacity: .7;
	transition: all .3s;

	@extend .cross-center;

	&:hover{
		// background-color: map-get($colors, color1);
		background-color: #000;
		color: #fff;
		// color: map-get($colors, color1);
		// opacity: 1;
	}
}

.lightbox-close{
	top: 1rem;
	right: 1rem;
	border-radius: 50%;
}

.lightbox-arrow-left, 
.lightbox-arrow-right{
	top: 50%;
	transform: translateY(-50%);
	font-size: $size-xxl;
	padding: 2rem 1rem;

	@include from-l(){
		padding: 3rem 1.5rem;
	}
}

.lightbox-arrow-left{
	left: 1rem;
}

.lightbox-arrow-right {
	right: 1rem;
}

.lightbox-item{
	// @extend .absolute-container;
	
	&__img{
		// position: absolute;
  //   top: 0;
  //   width: 100%;
  //   height: 100%;
  //   object-fit: contain;

  	position: absolute;
		top: 50%;
	  left: 50%;
	  width: auto;
	  // height: 100%;
	  transform: translate(-50%, -50%);

	  max-width: 90%;
   	height: auto;
   	max-height: calc(100% - 8rem);

	  @include from-l(){
	  	// top: 50%;
	  	// top: 0;
	  	// max-width: 90%;
    	height: 100%;
    	// transform: translate(-50%, 0);
	  }
	}
	
	&__data{
		color: #fff;
		position: absolute;
    left: 50%;
    bottom: 1.75rem;
    transform: translateX(-50%);
	}
}