  top:0;
  left:0;
  width:100%;
  height:100%;
}

.button{
  display:inline-block;
  padding:.6em 1.2em;
  cursor:pointer;
  border:none;
  text-decoration:none;
  background:#eee;
  color:#333;
  margin-right:1rem;
  margin-bottom:1rem;
}

.button:hover{
  background:#ddd;
}

.button.radius{
  border-radius:.25rem;
}

.button.round{
  border-radius:2em;
}

.button:last-of-type{
  margin-right:0;
}

:root{
  --gap:1rem;
}

.grid{
  display:-ms-grid;
  display:grid;
  grid-column-gap:2rem;
  width:calc(100vw - var(--gap) * 2);
  margin-left:1rem;
  margin-right:1rem;
}

@media screen and(min-width:1200px){
  .grid{
    width:1200px;
    margin-left:auto;
    margin-right:auto;
  }
}

.grid .grid,.grid.full{
  width:100%;
  max-width:100%;
  margin-right:0;
  margin-left:0;
}

