.masonry-layout {
  --columns: 1;
  // --gap: 1.2rem;
  --gap: 1rem;
  $columns: 6;
  
  display: grid;
  grid-template-columns: repeat(var(--columns),1fr);
  grid-gap: var(--gap);

  .masonry-item{
    position: relative;
    cursor: pointer;
    margin-bottom: var(--gap);

    // @include to-m(){
    //   margin-bottom: 1rem;
    // }
  }
  
  @for $i from 1 through $columns {
    &.columns-#{$i}{
      --columns: #{$i};
    }
  }
}